.description {
  color: $c-white;

  font-size: 60px;
  line-height: 60px;
  font-weight: bold;
  letter-spacing: -1.5px;

  &__suptitle,
  &__title {
    display: block;
  }
}

@media screen and (max-width:450px) {
  .description {
    &__suptitle {
      display: block;
    }
  }
}
