.button-form {
  background: $buttom-color;
  
  border: 1px solid $buttom-color;
  border-radius: 8px;
  
  color: $c-white;
  
  text-align: center;
  font-size: 16px;
  
  height: 56px;
  width: 100%;
  
  box-shadow: 0 6px 23px rgba(0, 117, 255, 0.46);
  &:hover {
    cursor: pointer;
  }
}
